import React from 'react';
import styled from 'styled-components';

const NavigationWrapper = styled.nav`
  display: flex;
  align-items: center;
`;

const NavigationList = styled.ul`
  display: flex;
  color: white;
  list-style: none;
`;

const NavItemWrapper = styled.li`
  color: ${p => p.active ? '#cfbdfb' : 'inherit'}
`;

const NavItemLink = styled.a`
  display: block;
  cursor: pointer;
  padding: 30px 25px;
`;

const NavigationItem = ({children, ...props}) => (
    <NavItemWrapper {...props}>
        <NavItemLink>
            {children}
        </NavItemLink>
    </NavItemWrapper>
);

const Navigation = ({children}) => (
    <NavigationWrapper>
        <NavigationList>
            {children}
        </NavigationList>
    </NavigationWrapper>
);

Navigation.Item = NavigationItem;

export { Navigation };


