import React from 'react';
import styled from "styled-components";

import {colors} from 'ui/theme';
import checkImage from 'assets/images/tick-copy-2.png';

const Field = styled.div`
  display: flex;
  flex-direction: column;
  ${p => p.width && `width: ${p.width};`}
`;

const Label = styled.label`
  color: white;
  font-size: .875rem;
`;

const Input = styled.input`
  margin-top: 10px;
  padding: 15px 0 15px 16px;
  border: 1px solid #ffffff;
  color: white;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .1);
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
  
  &::placeholder {
      font-size: 1rem;
      color: white;
      opacity: .6;
  }
`;

export const InputField = ({children, width, id, ...props}) => (
    <Field width={width}>
        <Label htmlFor={id}>{children}</Label>
        <Input id={id} {...props}/>
    </Field>
);

const RadioLabel = styled.label`
  display: block;
  padding: 15px;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .1);
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
`;

const RadioField = styled.div`
  ${p => p.marginRight && `margin-right: ${p.marginRight}`}
  ${p => p.marginLeft && `margin-left: ${p.marginLeft}`}
  
  input {
    display: none;
    
    &:checked + label  {
      background-color: #fff;
      color: ${colors.main};
    }
  }
`;

export const RadioButton = ({children, name, id, value, ...props}) => (
    <RadioField {...props}>
        <input type='radio' id={id} name={name} value={value}/>
        <RadioLabel htmlFor={id}>{children}</RadioLabel>
    </RadioField>
);

const CheckboxLabel = styled.label`
  width: 18px;
  height: 18px;
  display: block;
  position: relative;
  font-size: .875rem;
  background-color: #fff;
  cursor: pointer;
  
  &:after {
    content: '';
    display: block;
    cursor: pointer;
    width: 18px;
    height: 18px;
    background: no-repeat center center;
  }
`;

const CheckboxInputField = styled.div`
  margin-right: 15px;
    
  input {
    display: none;
  
    &:checked + label {
      &:after {
        background-image: url(${checkImage});
      }
    }
  }
`;

export const CheckboxField = ({name, id}) => (
    <CheckboxInputField>
        <input type="checkbox" name={name} id={id}/>
        <CheckboxLabel htmlFor={id}/>
    </CheckboxInputField>
);


