import React from 'react';
import styled from 'styled-components';

import {Container} from 'ui';

const HeroWrapper = styled.div`
  height: 90vh;
  padding-top: 120px;
`;

const HeroTitle = styled.h1`
  display: block;
  margin: 0;
  color: white;
  font-size: 3.75rem;
  text-align: center;
`;

const HeroSubtitle = styled.h2`
  font-size: 1.5rem;
  color: white;
  text-align: center;
  margin-top: 30px;
`;

export const Hero = () => (
    <HeroWrapper>
        <Container dir='column' align='center'>
            <HeroTitle>
                A Decentralized Identity <br/> Management <br/> Ecosystem
            </HeroTitle>
            <HeroSubtitle>
                Powered by Blockchain Technology <br/> and Tokenization
            </HeroSubtitle>
        </Container>
    </HeroWrapper>
);


