import React from 'react';
import styled from 'styled-components';

import {Container, Logo} from 'ui';
import logoImage from 'assets/images/logo.png';

const FooterContainer = Container.extend`
  padding: 20px 30px;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(238, 238, 238, .3);
`;

const FooterWrapper = styled.footer`
  background-image: linear-gradient(#6743df 100%, #743FE7 0%);
`;

const FooterList = styled.ul`
  display: flex;
  font-size: .75rem;
  color: white;
`;

const FooterListItem = styled.ul`
  margin: 0 15px;
`;

export const Footer = () => (
    <FooterWrapper>
        <FooterContainer>
            <Logo img={logoImage} src='#' alt='Company logo'/>
            <FooterList>
                <FooterListItem>
                    © Hive  2018 Hive, Inc.
                </FooterListItem>
                <FooterListItem>
                    <a href="">Terms</a>
                </FooterListItem>
                <FooterListItem>
                    <a href="">Privacy</a>
                </FooterListItem>
                <FooterListItem>
                    <a href="">Security</a>
                </FooterListItem>
            </FooterList>
        </FooterContainer>
    </FooterWrapper>
);


