import React from 'react';
import styled from 'styled-components';

const LogoLink = styled.a`
  display: flex;
  align-items: center;
`;

const Image = styled.img`
  width: 100%;
`;

export const Logo = ({link, img, alt}) => (
    <LogoLink href={link}>
        <Image src={img} alt={alt}/>
    </LogoLink>
);


