import React, {Component} from 'react';
import styled from 'styled-components';

import {Header, Hero, PrivateSale, Problems, ContactForm, Footer, Partners} from 'ui/templates';
import heroBgImage from 'assets/images/bg/header-mosaic-bg.png';

const AppWrapper = styled.main`
  background-size: 100%;
  background: url(${heroBgImage}) top center;
  background-repeat-y: no-repeat;
`;

class App extends Component {
    render() {
        return (
            <AppWrapper>
                <Header/>
                <Hero/>
                <PrivateSale/>
                <Problems/>
                <Partners/>
                <ContactForm/>
                <Footer/>
            </AppWrapper>
        );
    }
}

export default App;



