import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: ${p => p.dir ? p.dir : 'row'};
  justify-content: ${p => p.justify ? p.justify : 'flex-start'};
  align-items: ${p => p.align ? p.align : 'flex-start'};
  max-width: 1200px;
  margin: 0 auto;
`;




