import {createSymbiote} from 'redux-symbiote';

const initialState = {
    sending: false,
    error: false,
    success: null
};

export const {actions, reducer} = createSymbiote(initialState, {
    submit: state => ({...state, sending: true}),
    success: state => ({...state, sending: false, success: true, error: false}),
    error: state => ({...state, sending: false, success: false, error: true}),
}, 'form');

export const sendForm = (payload) => (
    async (dispatch) => {
        dispatch(actions.submit());

        try {
            await new Promise((resolve, reject) => {
                setTimeout(() => {
                    //TODO Send to server

                    if (Math.random() < .5) {
                        reject();
                    } else {
                        resolve();
                    }
                }, 2000);
            });

            dispatch(actions.success());
        } catch (e) {
            dispatch(actions.error());
        }
    }
);


