import React, {Component} from 'react';
import styled from 'styled-components';
import {bindActionCreators} from "redux";
import {sendForm} from "store/form-reducer";
import {connect} from "react-redux";

import {Container, InputField, RadioButton, CheckboxField, SocialsList, Spinner} from 'ui';
import {colors} from 'ui/theme';

import twitterIcon from 'assets/images/socials/twitter.png';
import linkedinIcon from 'assets/images/socials/linedin.png';
import instaIcon from 'assets/images/socials/insta.png';
import fbIcon from 'assets/images/socials/facebook.png';

import heroBgImageInverse from 'assets/images/bg/header-mosaic-bg-inverse.png';

const ContactFormWrapper = styled.section`
  padding-top: 150px;
  padding-bottom: 40px;
  background: url(${heroBgImageInverse}) bottom center;
`;

const ContactTitle = styled.h3`
  color: white;
  font-size: 2.5rem;
`;

const ContactSubtitle = styled.h4`
  margin-top: 37px;
  color: white;
  font-size: 1.125rem;
`;

const Form = styled.form`
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto 0 auto;
  width: 70%;
`;

const FormColumn = styled.div`
  width: 50%;
  padding: 30px;
`;

const FormRow = styled.div`
  display: flex;
  align-items: center;
  justify-content: center; 
  width: 100%;
`;

const InfoBlock = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 2rem;
  
  p {
    margin: 0;
  }
`;

const FormField = styled.div`
  display: flex;
  flex-direction: ${p => p.dir ? p.dir : 'column'};
  margin: 20px 0;
  width: 100%;
  ${p => p.margin && `margin: ${p.margin}`}
  ${p => p.width && `width: ${p.width};`}
`;

const FormFields = styled.div`
  display: flex;
  flex-direction: row;
`;

const Label = styled.label`
  color: white;
  font-size: .875rem;
  ${p => p.margin && `margin: ${p.margin}`}
`;

const InfoText = styled.p`
  color: white;
  font-size: .8rem;
`;

const ErrorText = styled.p`
  color: #ff6054;
  font-size: .8rem;
`;

const Button = styled.button`
  color: ${colors.main};
  background-color: #fff;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  height: 50px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
`;

class ContactFormUi extends Component {
    handleSubmit = (event) => {
        event.preventDefault();

        this.props.sendForm(event.target.firstName.value);
    };

    render() {
        return (
            <ContactFormWrapper>
                <Container dir='column' align='center'>
                    <ContactTitle>Hive Token Presale Form</ContactTitle>
                    <ContactSubtitle>Join the revolution in democratizing your data and retailers on the open
                        web</ContactSubtitle>
                    <Form onSubmit={this.handleSubmit}>
                        <FormColumn>
                            <FormField>
                                <InputField placeholder='Apple Incorporation' name='firstName' id='first-name'>First
                                    Name</InputField>
                            </FormField>
                            <FormField>
                                <InputField placeholder='Apple Incorporation' name='lastName' id='last-name'>Last
                                    Name</InputField>
                            </FormField>
                            <FormField>
                                <InputField placeholder='Apple Incorporation' name='reffered' id='reffered'>Who reffered
                                    you</InputField>
                            </FormField>
                        </FormColumn>
                        <FormColumn>
                            <FormField>
                                <InputField placeholder='Apple Incorporation' name='email' id='email'>Email
                                    Address</InputField>
                            </FormField>
                            <FormFields>
                                <FormField margin='0 15px 0 0'>
                                    <InputField placeholder='Amount' name='amount' id='amount'>Amount</InputField>
                                </FormField>
                                <FormField margin='0' width='auto'>
                                    <Label>Is the amount in</Label>
                                    <FormField dir='row' margin='10px 0 0'>
                                        <RadioButton name='amount' id='amount-eth' value='eth'>eth</RadioButton>
                                        <RadioButton marginLeft='15px' name='amount' id='amount-btc'
                                                     value='btc'>btc</RadioButton>
                                    </FormField>
                                </FormField>
                            </FormFields>
                            <FormField dir='row' margin='45px 0 0'>
                                <CheckboxField name='check' id='check'/>
                                <Label>I want to participate later, but I want to be
                                    aware of all the events</Label>
                            </FormField>
                        </FormColumn>
                        <InfoBlock>
                            {this.props.form.success && <InfoText>Your information has been sent</InfoText>}
                            {this.props.form.error && <ErrorText>Sorry, an error occured</ErrorText>}
                        </InfoBlock>
                        <FormRow>
                            <Button disabled={this.props.form.sending}>
                                {this.props.form.sending ? <Spinner size='1.2rem'/> : 'Send Information'}
                            </Button>
                        </FormRow>
                    </Form>
                    <SocialsList margin='40px 0 0'>
                        <SocialsList.Item img={twitterIcon} alt="twitter"/>
                        <SocialsList.Item img={fbIcon} alt="facebook"/>
                        <SocialsList.Item img={instaIcon} alt="instagram"/>
                        <SocialsList.Item img={linkedinIcon} alt="linkedin"/>
                    </SocialsList>
                </Container>
            </ContactFormWrapper>
        )
    }
}

function stateToProps(state) {
    return {
        form: {
            sending: state.form.sending,
            error: state.form.error,
            success: state.form.success
        },
    }
}

function dispatchToProps(dispatch) {
    return {
        sendForm: bindActionCreators(sendForm, dispatch),
    }
}

export const ContactForm = connect(stateToProps, dispatchToProps)(ContactFormUi);


