import {colors} from "ui/theme";
import styled from "styled-components";

import titleImage from 'assets/images/heading.png';

export const SectionHeader = styled.h3`
    color: ${colors.main};
    font-size: 2rem;
    position: relative;
    
    &:before {
      content: '';
      display: block;
      width: 50px;
      height: 43px;
      position: absolute;
      left: -30px;
      top: -35px;
      background: url(${titleImage}) no-repeat;
      background-size: cover;
    }
`;


