import React from "react";
import styled from "styled-components";

const ClipContainer = styled.div`
  position: relative;
  height: 200px;
  width: 100%;
`;

const Clip = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  clip-path: polygon(${p => p.path});
  background-color: ${p => p.color}; 
`;

const ClipPath = (props) => <ClipContainer {...props}/>;

ClipPath.Item = Clip;

export {ClipPath};


