import React from 'react';

import {Container, Logo, Navigation} from 'ui';
import logoImage from 'assets/images/logo.png';

export const Header = () => (
    <div>
        <Container justify='space-between' align='center'>
            <Logo img={logoImage} src='#' alt='Company logo'/>
            <Navigation>
                <Navigation.Item>Products</Navigation.Item>
                <Navigation.Item>For Businesses</Navigation.Item>
                <Navigation.Item active>For Developers</Navigation.Item>
                <Navigation.Item>Company</Navigation.Item>
                <Navigation.Item>Log in</Navigation.Item>
            </Navigation>
        </Container>
    </div>
);


