import React from 'react';
import styled from 'styled-components';

const List = styled.ul`
  display: flex;
  list-style: none;
  ${p => p.margin && `margin: ${p.margin}`}
`;

const Item = styled.li`
  display: flex;
  align-items: center;
  margin: 0 10px;
  cursor: pointer;
`;


const SocialsItem = ({img, alt, href}) => (
    <Item>
        <a href={href}><img src={img} alt={alt}/></a>
    </Item>
);

const SocialsList = ({children, ...props}) => (
    <List {...props}>{children}</List>
);

SocialsList.Item = SocialsItem;

export {SocialsList};


