import React from 'react';
import styled from 'styled-components';

import {SectionHeader, Container} from 'ui';
import partner1 from 'assets/images/partners/482-logo.png';
import partner2 from 'assets/images/partners/gh-logo-blue-green-1.png';
import partner3 from 'assets/images/partners/logo--2-.png';
import partner4 from 'assets/images/partners/mt-logo.png';

const PartnersWrapper = styled.section`
  margin-top: 100px;
  margin-bottom: 100px;
`;

const PartnersList = styled.ul`
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 100px;
`;

const PartnersListItem = styled.li`
  display: flex;
  justify-content: center;
  flex-basis: 280px;
  margin-bottom: 50px;
`;

const ParthnersItem = ({href, img, alt}) => (
    <PartnersListItem>
        <a href={href}><img src={img} alt={alt}/></a>
    </PartnersListItem>
);

const parthners = [
    {href: '#', img: partner1, alt: 'partner1'},
    {href: '#', img: partner2, alt: 'partner2'},
    {href: '#', img: partner3, alt: 'partner3'},
    {href: '#', img: partner4, alt: 'partner4'},
];

export const Partners = () => (
    <PartnersWrapper>
        <Container dir='column' align='center'>
            <SectionHeader>TGE partners</SectionHeader>
            <PartnersList>
                {parthners.map((item, key) => <ParthnersItem key={key} href={item.href} img={item.img} alt={item.alt}/>)}
            </PartnersList>
        </Container>
    </PartnersWrapper>
);


